import React, { useEffect, useState } from "react"
import { navigate } from "gatsby"
import Footer from "../components/footer"
import "../css/index.css"
import 'animate.css'

import VerywiseLogo from "../images/verywiselogo.svg"
import TelegramLogo from "../images/telegramlogo.svg"
import Lares from "../images/twitter_lares.png"

/**
 * The start page of the app. When the user enters a search term, it passes it through to
 * /chat with query param.
 */
const IndexPage = () => {
  const [inputValue, setInputValue] = useState('');
  const [placeholderList, setPlaceholderList] = useState([
    'Can my landlord increase my rent?',
    'My landlord won\'t carry out repairs?',
    'Can my landlord evict me?',
    'How do I get my deposit back?',
    'Can I leave before the tenancy ends?'
  ])
  const [placeholderValue, setPlaceholderValue] = useState('');

  useEffect(() => {
    const placeholderText = getPlaceholderText();
    let typed = '';

    placeholderText.split("").forEach((character, i) => {
      setTimeout(() => {
        typed += character
        setPlaceholderValue(typed)
      }, 50 * i)
    })
  }, [])

  useEffect(() => {
    const interval = setInterval(() => {
      const placeholderText = getPlaceholderText();

      if (placeholderText === placeholderValue) {
        clearInterval(interval);
      }

      let typed = '';

      placeholderText.split("").forEach((character, i) => {
        setTimeout(() => {
          typed += character
          setPlaceholderValue(typed)
        }, 100 * i)
      })

    }, 4000);
  
    return () => clearInterval(interval);
  }, [])

  const handleSubmit = (e) => {
    e.preventDefault();
    const searchForm = document.querySelector('.search');
    searchForm.classList.add('animate__fadeOutDown')

    const laresQuote = document.querySelector('.lares-quote')
    laresQuote.classList.add('animate__fadeOutDown')

    setTimeout(() => {
      navigate(`/chat?q=${encodeURIComponent(inputValue)}`)
    }, 1000)
  }

  const randomInt = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  const getPlaceholderText = () => {
    return placeholderList[randomInt(0, placeholderList.length - 1)];
  }

  return (
    <>
        <main>
          <nav>
            <a href="/">Home</a>
            <a href="/about">About</a>
            <a href="https://t.me/renter_support_bot" target="_blank" rel="noopener">Telegram <img className="telegram-logo" src={TelegramLogo} height={22} width={22} alt="Telegram" /></a>
          </nav>
          <div>
            
            <header>
              <h1>
                <a href="/"><img className="logo" src={VerywiseLogo} alt="Very Wise - Be Empowered" /></a>
                <span className="invisible">Very Wise - Be Empowered</span>
              </h1>
              <h2>Be Empowered</h2>
            </header>
            <section className="search animate__animated animate__fast">
              <form onSubmit={handleSubmit}>
                <input className="search__input" type="text" value={inputValue} onChange={(e) => setInputValue(e.target.value)} placeholder={placeholderValue} />
                <input disabled={inputValue ? false : true} className={`search__submit`} type="submit" value="Search" />
              </form>
            </section>
            <section className="lares-quote animate__animated animate__faster">
              <p>The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being.</p>
              <p>Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.</p>
            </section>
          </div>
        </main>
        <Footer />
    </>
  )
}

export default IndexPage

export const Head = () => <>
  <title>Very Wise - Be Empowered</title>
  <meta name="description" content={`The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being. Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.`} />
  <meta name="image" content={`https://www.verywise.io/${Lares}`} />
  <meta name="twitter:card" content="summary_large_image" />
  <meta name="twitter:title" content="Very Wise - Be Empowered" />
  <meta name="twitter:url" content="https://www.verywise.io" />
  <meta name="twitter:description" content={`The name "Lares" comes from ancient Rome, where it was associated with guardian spirits that watched over households and ensured their well-being. Today, the name represents our AI chatbot that provides information and assistance to renters with any problems they encounter.`} />
  <meta name="twitter:image" content={`https://www.verywise.io/${Lares}`} />
  <meta name="twitter:creator" content="veriwise" />
</>
